import CircularProgress from '@material-ui/core/CircularProgress';
import {useDropzone} from 'react-dropzone';
import './status.css';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
const Teme = createMuiTheme({ palette: {primary:{main:blue[400]}} });
/*cookie*/
const theme = createMuiTheme();
const blueTheme = createMuiTheme({ palette: {main: blue[100]} })
const buttonTheme = createMuiTheme({ palette: { background:green }})
const margin=style ({
    marginLeft: theme.spacing.unit,
    width:"80%",
  })
const button=style ({
    marginLeft: theme.spacing.unit,
    backgroundColor:green,
  })
const appbar = style({
    flexGrow: 1,
})
const typo = style({
    fontFamily: "arial",
    fontWeight: 200,
    color: grey[50],
})
const mail = style({
    fontSize: "26px",
})
const dot = style({
  backgroundColor:"#C7F0EE",
  borderRadius:"50%",
  height:"120px",
  width:"120px",
  border:"2px solid #FC0FE8",
  borderStyle:"dotted",
})
const status_img=style({
  borderRadius:"50%",
  height:"115px",
  width:"116px",
})
const toolbar = style({
    backgroundColor: blue[400],
})
let Theme = createMuiTheme({ shadows: [0] });

let Status=props=> {
    const [state,setState]=useState({
        url:"",
    })
const {acceptedFiles, getRootProps, getInputProps} = useDropzone();

let drop_call =() =>{
  let Data=[];
    if(acceptedFiles.length>0){
      acceptedFiles.map(ex=>{
        Data=ex;
      });
      // setState({...state,
      //   url:URL.createObjectURL(Data),
      // })
      return <img src={URL.createObjectURL(Data)} className={status_img}/>
    }
}


  


  return (
      <div className="container">
      <MuiThemeProvider theme={Theme}>
                        
                        <AppBar>
              <Toolbar className={toolbar}>
              <Button color="inherit"  variant="outlined" exact to="/signUp">
              <FaEnvelopeOpen className={mail}/>&nbsp;&nbsp;Message</Button>
       <Grid container container
        direction="row"
        justify="flex-end"
        alignItems="flex-end"> 
              <Button color="inherit"><FaRss/>&nbsp;&nbsp;
              <Link to="/status" style={{color:"white"}}>status</Link></Button>
           
              <Button color="inherit" ><FaUserAlt/>&nbsp;&nbsp;Logout</Button>
      </Grid>
              </Toolbar>

            </AppBar>
            </MuiThemeProvider>
    <br/>
            <br/>
            <br/>
          
  <Grid container>
            <Grid item xs={12} md={12} sm={12}>
  <Card>
              <section className="container" align="left">
      <div {...getRootProps({className: 'dropzone'})}>
        <input {...getInputProps()} />
            <br/>
            <div className={dot}>
              {drop_call()}
            </div>  
      </div>
    
    </section>

<div style={{marginLeft:"220px",marginBottom:"10px"}}>
    <Button variant="contained" style={{backgroundColor:"#0FBBF3",color:"white"}}>Submit</Button>

</div> 
  </Card>
</Grid>
</Grid>
  <br/>
<Grid container>
            <Grid item xs={12} md={12} sm={12}>  
{/*card is start*/}
<Card>
  <List >
      <ListItem alignItems="flex-start">
        <ListItemAvatar>
          <Avatar src="data:image/jpeg;base64," alt="Remy Sharp" style={{width:"56px",height:"56px"}} />
        </ListItemAvatar>
      &nbsp;
      &nbsp;
      &nbsp;
        <ListItemText
          primary="Nikunj Gajjar"
          secondary={
            <React.Fragment>
              <Typography
                component="span"
                variant="body2"
                color="textPrimary"
              >
              </Typography>
              {"July 20, 2014"}
            </React.Fragment>
          }
        />
      </ListItem>
</List>
</Card>
{/*card is end*/}
<br/>
{/*
  Darshit Gajjar card is start
*/}
<Card>
  <List >
      <ListItem alignItems="flex-start">
        <ListItemAvatar>
          <Avatar src="data:image/jpeg;base64," alt="Remy Sharp" style={{width:"56px",height:"56px"}}/>
        </ListItemAvatar>
      &nbsp;
      &nbsp;
      &nbsp;
        <ListItemText
          primary="Darshit Gajjar"
          secondary={
            <React.Fragment>
              <Typography
                component="span"
                variant="body2"
                color="textPrimary"
              >
              </Typography>
              {"August 22, 2014"}
            </React.Fragment>
          }
        />
      </ListItem>
</List>
</Card>
{/*card is end*/}
</Grid>
</Grid>
        </div>

    )

}

export default Status;